@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Narrow&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html, #root, .app, .container {
	width: 100%;
	height: 100%;
	margin: 0px;
	padding: 0px;
}

.hidden {
	display: none;
	opacity: 0;
}

.nav-logo {
	width: 20%;
	height: 20%;
}
*{padding:0px;box-sizing:border-box;margin:0px}nav{position:absolute;width:100vw;display:flex;justify-content:space-between;align-items:center;min-height:12vh;background-color:transparent}nav a{font-family:"Asap", sans-serif;text-decoration:none;font-weight:bold;font-size:16px;color:white;cursor:default;padding:10px 18px 10px 18px;transition:180ms all ease-in-out}nav a:hover{background-color:rgba(220,220,220,0.4)}.nav-left{display:flex;align-items:center;width:auto;margin-left:8%}.nav-logo{color:#fff;font-size:16px;letter-spacing:1px;width:auto}.nav-links{display:flex;justify-content:space-around;width:30%;text-decoration:none;margin-right:8%}.nav-links a{text-decoration:none;color:white;letter-spacing:1px;font-weight:bold;font-size:14px}.nav-links a:hover{background-color:rgba(220,220,220,0.4)}.nav-links li{list-style:none}.burger{transition:200ms all ease-in-out;display:none;cursor:pointer;z-index:1005}.burger:hover div{background-color:#aaa}.burger div{width:1.5rem;height:2px;background-color:white;margin:5px;transition:100ms all ease-in-out}.burger div:hover{background-color:#aaa}@media screen and (max-width: 768px){body{overflow-x:hidden}.nav-links{position:absolute;top:0px;left:0px;height:100vh;background-color:rgba(220,220,220,0.4);margin:0px;display:flex;flex-direction:column;align-items:center;width:40%;transform:translateX(-100%);transition:transform 0.3s ease-in}.nav-links li{transition:opacity 0.3s ease-in;opacity:0}.nav-logo{position:absolute;right:8%}.burger{display:block}}.nav-active{transform:translateX(0%)}@-webkit-keyframes navLinkFade{from{opacity:0;transform:translateX(-50px)}to{opacity:1;transform:translateX(0px)}}@keyframes navLinkFade{from{opacity:0;transform:translateX(-50px)}to{opacity:1;transform:translateX(0px)}}.toggle .line1{transform:rotate(-90deg) translate(-2px, 8px)}.toggle .line2{transform:rotate(90deg) translate(-5px, 0px)}.toggle .line3{transform:rotate(90deg) translate(-12px, 8px)}

.center{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.full-screen{width:100vw;height:100vh}.parallax-image{background-image:url("https://source.unsplash.com/random");background-repeat:no-repeat;height:100vh;width:100%;background-position:center;background-size:cover;background-attachment:fixed;display:flex}.image-section-container h1,.image-section-container h3{color:#dcdcdc}.my-work-title{padding-top:30px;padding-bottom:30px;border-bottom:1px solid rgba(220,220,220,0.6)}.my-work-title a{font-size:16px;margin-top:20px;margin-bottom:20px}.my-work{background-color:#222;color:white;font-family:"Open Sans", sans-serif;font-size:18px}.my-work-description{font-size:16px;padding-top:40px;padding-bottom:40px}.container-center{padding-right:15px;padding-left:15px;margin-right:15%;margin-left:15%;padding-top:40px;padding-bottom:40px}

.project-card{width:200px;height:200px}.project-image{width:100%;height:100%}




